import React, { Component } from "react";
import axios from "axios";
import "../../styles.less";
import fetch from "isomorphic-unfetch";
import { Card, Row, Col, Button } from "antd";
const { Meta } = Card;

export default class ArtistList extends Component {
  state = { profiles: [], count: 0, artists: [] };

  componentDidMount() {
    this.getData(res => {
      this.setState({
        initLoading: false,
        artists: res,
        count: this.state.count + 10
      });
    });
  }

  getData = callback => {
    axios
      .get(`/api/profiles/artistProfiles/10/${this.state.count}`)
      .then(res => callback(res.data));
  };

  onLoadMore = () => {
    this.setState({
      loading: true,
      count: this.state.count + 10
    });
    this.getData(res => {
      const artists = this.state.artists.concat(res);
      this.setState(
        {
          artists,
          loading: false
        },
        () => {
          // Resetting window's offsetTop so as to display react-virtualized demo underfloor.
          // In real scene, you can using public method of react-virtualized:
          // https://stackoverflow.com/questions/46700726/how-to-use-public-method-updateposition-of-react-virtualized
          window.dispatchEvent(new Event("resize"));
        }
      );
    });
  };

  componentWillMount() {}
  render() {
    const { profiles } = this.state;
    return (
      <React.Fragment>
        <div className="center">
          <div style={{ background: "#ECECEC", padding: "30px" }}>
            <Row gutter={16}>
              <h1 className="h1Heading">Artists on our platform</h1>
              {artists.map(profile => (
                <h2 key={profile._id}>
                  <Col span={8}>
                    <Card
                      hoverable
                      style={{ width: 240 }}
                      bordered={false}
                      onClick={() => clickModal(category)}
                    >
                      <Meta
                        title={
                          <div>
                            {/* <i className="large material-icons">
                              {profile.icon}
                            </i> */}
                            <br />
                            {profile.companyName}
                          </div>
                        }
                      />
                    </Card>
                  </Col>
                </h2>
              ))}
            </Row>
            <Button onClick={() => this.onLoadMore()}>View More</Button>
          </div>
          <style jsx>{`
            .h1Heading {
              text-align: center;
              color: #595959;
            }
            h2 {
              display: inline-block;
            }
            .center {
              text-align: center;
            }
            i {
              font-size: 50px;
            }
          `}</style>
        </div>
      </React.Fragment>
    );
  }
}
